import React, { Component, Fragment } from 'react'
import Layout from '../components/Layout'
import ContactUs from '../components/templates/landing-page/ContactUs/'


export default class extends Component {
    render(){
        return(
            <Layout>
                <br/>
                <section className='hero is-medium is-dark is-bold'>
                    <div className='hero-body'>
                        <div className='container has-text-centered'>
                            <h1 className='title is-size-2'>Company's Profile</h1>
                        </div>
                    </div>
                </section>
                <section className='section'>
                        <div className='container'>
                            <div className='columns'>
                                <div className='column'>
                                    <figure class="image is-square">
                                        <img src="/img/logo -800x300.png" alt="Placeholder image"/>
                                    </figure>
                                
                                </div>
                                <div className='column is-7-desktop'>
                                    <div class="content ">
                                        <p>
                                            TekXul is a Ghanaian founded company whose vision is to 
                                            help conserve as much water as possible for posterity 
                                            by providing a smart water management system that leverages 
                                            on the internet of things.
                                        </p>
                                        <p>
                                            Our flagship product, the Smart Water manager is system that 
                                            monitors how much water is in a storage tank and communicates 
                                            that wirelessly to its corresponding pump controller or actuator. 
                                            When the water reaches a set point, it automatically turns of the 
                                            pump or water source to keep the storage tank from overflowing. 
                                            Also, the system can automatically refill itself. Our system is 
                                            not restricted to only one tank but can control multi-tank system.   
                                        </p>
                                        <p>
                                            TekXul is co-founded by Sagoe Gad Lawson. I had my first degree 
                                            in BSc. Materials Science and Engineering at Kwame Nkrumah 
                                            University of Science and Technology. I became a mentor for 
                                            CYGNUS (a summer program on robotics for S.H.S) at KNUST. 
                                            I have had a marketing and research field experience with Ipsos 
                                            at Asylum down, Kwame Nkrumah Circle - Accra and Marketing Support 
                                            Consultancy (MSC) at Abofu, Achimota Accra. Before co-founding TekXul, 
                                            Sagoe worked with Kumasi Hive as Robotics and Electronics lead. 
                                            Projects completed at Kumasi Hive entails: Weather Station, 
                                            IoT Scale for farm bridge, Smart home devices for Ampersand, 
                                            GPS tracking device for Cobat and successful training programs 
                                            on robotics for KNUST students.     
                                        </p>
                                        <p>
                                            We have currently installed 5 devices in homes across Ghana and 
                                            have helped save an estimated amount 240, 000 gallons of water 
                                            over a period of 4 months, not to mention energy savings on 
                                            electricity! The Smart Water Manager (SWAM) will help save over 
                                            a trillion more gallons of water now and for posterity. We are 
                                            currently looking into models that allow us to transfer the cost 
                                            saved on water to needy communities in Ghana. 
                                        </p>
                                    </div>
                                </div>
                            </div>
                            
                        </div>
                </section>
                <ContactUs/>
            </Layout>
        )
    }
}


