import React, { Component, Fragment } from 'react'
import Layout from '../components/Layout/'
import Intro from '../components/templates/landing-page/Intro/'
import Installation from '../components/templates/landing-page/Installation/'
import InstallationVideo from '../components/templates/landing-page/Installation-Video/'
import Benefits from '../components/templates/landing-page/Benefits/'
import ModelPrices from '../components/templates/landing-page/ModelPrices/'
import ContactUs from '../components/templates/landing-page/ContactUs/'


export default class extends Component {
    render(){
        return(
            <Layout>
               <Intro/>
               <Benefits/>
               <InstallationVideo/>
               <Installation/>
               <ModelPrices/>
               <ContactUs/>
            </Layout>
        )
    }
}
