import React, { Component } from 'react'

export default class extends Component {
    render(){
        const { active } = this.state
        return(
            <nav className="navbar  is-fixed-top" role="navigation" aria-label="main navigation">
                <div className='container'>
                    <div className="navbar-brand">
                        <a className="navbar-item" href="/">
                            {/* <img src="https://bulma.io/images/bulma-logo.png" /> */}
                            <img src="/img/logo-400X190.png" />
                        </a>

                        <a 
                            role="button" 
                            onClick={this.toggleActive}
                            className="navbar-burger burger" 
                            aria-label="menu" 
                            aria-expanded="false" 
                            data-target="navbarBasicExample">
                            <span aria-hidden="true"></span>
                            <span aria-hidden="true"></span>
                            <span aria-hidden="true"></span>
                        </a>
                    </div>

                    <div id="navbarBasicExample" className={`navbar-menu ${active? 'is-active' : null}`}>
                        <div className="navbar-start"></div>

                        <div className="navbar-end">
                            <a href='/' className="navbar-item">Home</a>
                            <div className="navbar-item has-dropdown is-hoverable">
                                <a className="navbar-link">SWAM</a>

                                <div className="navbar-dropdown">
                                    <a href='/#installation' className="navbar-item">Installation</a>
                                    <a href='/#modelPricing' className="navbar-item">Model Pricing</a>
                                    <a className="navbar-item">User Manual</a>
                                    <hr className="navbar-divider"/>
                                    <a className="navbar-item">Report an issue</a>
                                </div>
                            </div>
                            <a href='/#services' className="navbar-item">Our Services</a>
                            <a href='/#contactUs' className="navbar-item">Contact</a>
                            <a href='/aboutUs' className="navbar-item">About Us</a>
                        </div>
                    </div>
                </div>
            </nav>
        )
    }
    
    toggleActive(){
        this.setState(prevState => ({
            active: !prevState.active
        }))
    }
    constructor(props){
        super(props)
        this.state = { active: false }
        this.toggleActive = this.toggleActive.bind(this)
    }
}
const NavbarItem = ({ currentPath, path }) => (
    <a 
        href={path}
        className={`navbar-item ${currentPath == path? 'is-active' : null}`}>
        Our Services
    </a>
)