import React from 'react'
import './style.css'

export default () => (
    <section className="installation-video hero ">
        <div className='hero-body'>
            <div className='columns'>
                <div className='column'>
                    <iframe 
                        src={`https://www.youtube.com/embed/XPPVWuUGktM`} 
                        frameBorder="0" 
                        allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" 
                        allowFullScreen>
                    </iframe>
                </div>
                <div className='column'>
                    <iframe 
                        src={`https://www.youtube.com/embed/dUhqpazPFnQ`} 
                        frameBorder="0" 
                        allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" 
                        allowFullScreen>
                    </iframe>
                </div>
                
            </div>
            
        </div>
    </section>
)