import React from 'react'
import './style.css'

const benefits = [
    {
        icon: '/img/stats.svg',
        title: 'Water Management',
        note: 'SWAM helps to monitor and controls the amount of water in storage tanks by automatically turning on and off pumps.'
    },
    {
        icon: '/img/happy.svg',
        title: 'Convenience',
        note: 'SWAM ensures constant supply of water to its users and customers by eliminating the hustle of mankind manually turning on and off pumps.'
    },
    {
        icon: '/img/wallet.svg',
        title: 'Save Money',
        note: 'SWAM helps customers to save money that could be used to pay for extra bills (water bills and electricity bills) that occurs during overflow.'
    },
    
]

export default ({  }) => (
    <section id='services' className=' hero benefits has-bg-img' style={{backgroundImage: "url('/img/tap.jpeg')"}}>
        <div className='hero-body'>
            <div className='container has-text-centered'>
                <h1 className="title is-size-1">
                    SWAM Offers
                </h1>

                {/* Benefits */}
                <div className='columns is-multiline'>
                    {
                        benefits.map((benefit, key) => (
                            <div key={key} className='column is-4-desktop'>
                                <Benefit {...benefit}/>
                            </div>
                        ))
                    }
                </div>
                <a href='#' className='button is-outlined is-medium' >Learn More</a>
            </div>
        </div>  
    </section>
)

const Benefit = ({ icon, title, note }) => (
    <a className='benefit'>
        <img src={icon} alt=''/>
        <h2 className='benefit-title is-size-4'>{title}</h2>
        <p className='benefit-note'>{note}</p>
    </a>
)