import React, { Component, Fragment } from "react";
import "./style.css";

const model = [
  {
    title: "Single Tank",
    subtitle: "$142.00",
    featuredImage: "/img/t1.png",
    features: [
      "Solar Powered Transmitter",
      "Automatic Controller",
      "Indicator Panel",
      "Emergency Switch",
      "Embedded Alarm System",
      "Reset Function"
    ]
  },
  {
    title: "Double Tank",
    subtitle: "$254.00",
    featuredImage: "/img/t2.png",
    features: [
      "Solar Powered Transmitter",
      "Automatic Controller",
      "Indicator Panel",
      "Emergency Switch",
      "Automatic Selector",
      "Embedded Alarm System",
      "Reset Function"
    ]
  },
  {
    title: "Tripple Tank",
    subtitle: "$395.00",
    featuredImage: "/img/t3.png",
    features: [
      "Solar Powered Transmitter",
      "Automatic Controller",
      "Indicator Panel",
      "Emergency Switch",
      "Automatic Selector",
      "Embedded Alarm System",
      "Reset Function"
    ]
  },
  {
    title: "Quadriple Tank",
    subtitle: "$482.00",
    featuredImage: "/img/t4.png",
    features: [
      "Solar Powered Transmitter",
      "Automatic Controller",
      "Indicator Panel",
      "Emergency Switch",
      "Automatic Selector",
      "Embedded Alarm System",
      "Reset Function"
    ]
  }
];

export default class extends Component {
  render() {
    return (
      <section id="modelPricing" className="section">
        <br />
        <div className="container has-text-centered">
          <h1 className="title is-size-1-desktop">Model Pricing</h1>
          <br />
        </div>
        <div className="columns is-multiline">
          {model.map((model, key) => (
            <div key={key} className="column is-6">
              <Modal key={key} model={model} />
            </div>
          ))}
        </div>
      </section>
    );
  }
}

const Modal = ({ model }) => (
  <a className="model has-text-centered">
    <article className="">
      <h2 className="is-size-3">{model.title}</h2>
      <h3 className="">{model.subtitle}</h3>
      <img src={model.featuredImage} alt="" />
      <div className="model-features">
        <h2 className="is-size-3">Features</h2>
        <div className="features">
          {model.features.map((feature, key) => (
            <span key={key} className="tag is-primary is-medium">
              {feature}
            </span>
          ))}
        </div>
      </div>
    </article>
  </a>
);
